export default {
  namespaced: true,
  state: {
    currentUser: {},
    currentPermission: [],
    permissions: {
      'modify_server_info': 33,  // 
      'start_stop_server': 32,  // 
      'delete_server': 31,  // 
      'add_server': 30,  // 
      'add_project': 1,  // 
      'delete_project': 8,  // 
      'build_project': 9,  // 
      'deploy_project': 10,  // 
      'modify_project_info': 11,  // 
      'delete_project_version': 12,  // 
      'start_stop_container': 13,  // 
      'delete_container': 14,  // 
      'add_directory': 2,  // 
      'delete_file': 20,  // 
      'copy_file_url': 19,  // url
      'preview_file': 18,  // 
      'delete_directory': 16,  // 
      'upload_file': 15,  // 
      'download_file': 17,  // 
      'view_company_identify_info': 34,  // 
      'company_identify': 21,  // 
      'modify_company_info': 3,  // 

      'dismiss_employee': 26,  // 
      'view_employee_info': 4,  // 
      'set_join_conditions': 27,  // 
      'set_employee_permission': 25,  // 
      'set_admin': 24,  // 
      'audit_employee': 23,  // 
      'view_employee_id_info': 22,  // 

      'modify_permission_template': 28,  // 
      'delete_permission_template': 29,  // 
      'add_permission_template': 5,  // 

      'audit_user_id': 6,  // 
      'company_issue': 7,  // 
      'audit_company_id': 35,  // 
      'invite_new_employee': 36  // 
    }
  },
  mutations: {
    'UPDATE' (state, data) {
      // 
      state.currentUser = data
    },
    'GET_USER' () {
      // 
    },
    'getPermission' (state, data) {
      let arry = []
      let arryData = []
      if (Array.isArray(data.permissions)) {
        data.permissions.forEach(item => {
          arryData.push(item.id)
        })
        data.permissions = arryData
      }
      if (Array.isArray(data.access_servers)) {
        arryData = []
        data.access_servers.forEach(item => {
          arryData.push(item.id)
        })
        data.access_servers = arryData
      }
      if (Array.isArray(data.access_projects)) {
        arryData = []
        data.access_projects.forEach(item => {
          arryData.push(item.id)
        })
        data.access_projects = arryData
      }
      if (Array.isArray(data.access_filehub)) {
        arryData = []
        data.access_filehub.forEach(item => {
          arryData.push(item.id)
        })
        data.access_filehub = arryData
      }
      arry.concat(data.permissions, data.access_servers, data.access_projects, data.access_filehub)
      state.currentPermission = arry
    },
    isPermission (state, permissionName) {
      return state.currentPermission.indexOf(state.permissions.permissionName) === -1
    }
  }
}



